import React from "react";
import {Tabs} from 'antd-mobile'
import './resumeList.scss'

export default ()=>{
    return (
        <>
            <Tabs>
                <Tabs.Tab title='已处理' key='tread'>
                    已处理
                </Tabs.Tab>
                <Tabs.Tab title='未处理' key='untread'>
                    未处理
                </Tabs.Tab>
            </Tabs>
        </>
    )
}